import Typography from '@buzzguru/ui/Typography2';
import FormSubmit from '@lskjs/form/FormSubmit';
import T from '@lskjs/t';
import { FastField, Form } from 'formik';
import { inject } from 'mobx-react';
import PropTypes from 'prop-types';
import React from 'react';
import Link from '@lskjs/link';

import { CenterWrapper } from './Login.styles';

const AuthLoginFormView = ({ control, status, errors, i18 }) => {console.log({errors});return(
  <>
    <Typography view="huge-10" color="#ffffff" paragraph>
      <T name="signInView.entrance" />
    </Typography>
    <Form className="ant-form ant-form-vertical">
      <FastField {...control('email')} placeholder={i18.t('authForm.email')} />
      <FastField
        {...control('password')}
        placeholder={i18.t('authForm.password')}
        info={(
          <Link href="https://buzzguru.com/auth/restore" tabIndex="-1" style={{ color: '#7070ff', textDecoration: 'none' }}>
            <T name="signInView.forgotPassword" />
          </Link>
        )}
      />
      <FormSubmit block status={status} errors={errors} style={{ padding: 18, margin: '48px auto 0', width: '90%' }}>
        <T name="buttons.login" />
      </FormSubmit>
    </Form>
    <CenterWrapper>
      <Typography view="title-5" color="#9b9b9b" style={{ paddingRight: 4, }}>
        <T name="signInView.newUser" />
      </Typography>
      <Typography view="title-6">
        <Link href="https://buzzguru.com/auth/signup" style={{ color: '#7070ff', textDecoration: 'none' }}>
          <T name="signInView.signUp" />
        </Link>
      </Typography>
    </CenterWrapper>
    <Typography view="title-2" color="#9B9B9B" style={{ marginLeft: 'auto', marginTop: 96 }}>
      {`Copyright © ${new Date().getFullYear()}`}
    </Typography>
  </>
);}

AuthLoginFormView.propTypes = {
  control: PropTypes.func.isRequired,
  status: PropTypes.string,
  errors: PropTypes.objectOf(Object),
  i18: PropTypes.objectOf(Object).isRequired,
};

AuthLoginFormView.defaultProps = {
  status: undefined,
  errors: undefined,
};

export default inject('i18')(AuthLoginFormView);
