import BuzzGuru from '@buzzguru/ui/assets/icons/buzzguru-logo-big';
// import Quality from '@buzzguru/ui/kit/Quality';
// import Button from '@lskjs/button';
import Tooltip from '@buzzguru/ui/Tooltip';
import Link from '@lskjs/link';
import truncate from 'lodash/truncate';
import { inject } from 'mobx-react';
import PropTypes from 'prop-types';
import React from 'react';

import Avatar from '../../Avatar';
import Stats from '../../BottomContent/Stats';
import Mention from '../../BottomContent/Mention';
import FlagIcon from '../../FlagIcon';
import { FlagWrapper, globalStyles, LanguageWrapper, Quality, Title, Wrapper } from './LeftContent.styles';
import Relations from './Relations';

globalStyles();

const LeftContent = ({ entity, view, i18 }) => {
  const { item } = entity;
  const general = item?.generalScore || item?.channel?.generalScore || 0;
  const isAuth = window?.__lskjs?.GM_getValue('session') || null;
  return (
    <Wrapper>
      <Link href={'https://buzzguru.com/home'}>
        <BuzzGuru width="144px" style={{ marginRight: 22 }} />
      </Link>
      {view === 'short' ? (
        <>
          <Avatar
            title={item?.name || item?.channel?.name}
            src={item?.avatar || item?.channel?.avatar}
            defaultAvatar="https://bzzguru.com/assets/images/blogger.png"
            size={36}
            cspStyle={{ marginRight: 12, borderRadius: '50px' }}
            style={{ marginRight: 12 }}
          />
          <Title>{truncate(item?.name || item?.channel?.name, { length: 20 })}</Title>
        </>
      ) : ''}
      {/* <Button
        style={{ width: 24, height: 24, borderRadius: 4, backgroundColor: '#4A4A4A80', marginRight: 12 }}
        view="empty"
        isRipple={false}
        icon={<FlagIcon code={item?.channelInfo?.country || ''} style={{ width: 16, height: 12 }} />}
      /> */}
      {(item?.country || item?.channel?.country) ? (
        <FlagWrapper>
          <FlagIcon code={item?.country || item?.channel?.country || ''} style={{ width: 16, height: 12, maxWidth: 'none' }} />
        </FlagWrapper>
      ) : (
        ''
      )}
      {(item?.language || item?.channel?.language) ? (
        <Tooltip
          id={`ex-lang-${item?.language || item?.channel?.language}`}
          place="top"
          overlay={i18.t(`locales.${item?.language || item?.channel?.language}`)}
        >
          <LanguageWrapper>{String(item?.language || item?.channel?.language).toUpperCase()}</LanguageWrapper>
        </Tooltip>
       ) : (
        ''
       )}
      {view === 'short' ? '' : (
        <Quality percent={general} />
      )}
      {view === 'short' ? (
        <>
          <Relations entity={entity} />
          <Quality percent={general} />
        </>
      ) : (
        <Stats entity={entity} style={{ marginLeft: 16 }} />
      )}
      {isAuth && view === 'long' ? <Mention entity={entity} style={{ marginLeft: 16 }} /> : ''}
    </Wrapper>
  );
};

LeftContent.propTypes = {
  entity: PropTypes.objectOf(Object).isRequired,
  i18: PropTypes.objectOf(Object).isRequired,
  view: PropTypes.oneOf(['short', 'long']),
};
LeftContent.defaultProps = {
  view: 'short',
};

export default inject('i18')(LeftContent);
