const Mention = ({ entity, i18 }) => {
  const { item: baseItem } = entity;
  const [type, setType] = useState('youtube');
  let item = baseItem;
  if (type === 'youtubeVideo' || type === 'twitchStream') {
    item = baseItem.channel;
  }
  const appMentions = item?.mentionsApps || {};
  const gameMentions = item?.mentionsGames || {};
  const mentionsChannels = item?.mentionsChannels || [];
  const siteMentions = item?.mentionsSites || {};

  const appsTotal = appMentions?.totals?.total || appMentions?.totals?.totalUniqProductsCount || 0;
  const gamesTotal = gameMentions?.totals?.total || gameMentions?.totals?.totalUniqProductsCount || 0;
  const sitesTotal = siteMentions?.totals?.total || siteMentions?.totals?.totalUniqProductsCount || 0;
  useEffect(() => {
    getRequestParams().then((res) => setType(res.type || 'youtube'));
  }, [document?.location?.href || '']);
  return (
    <Wrapper>
      {(appMentions.items || []).length ? (
        <Link target="_blank" href={`https://buzzguru.com/${type}/channels/${item?._id}/products`}>
          <Tooltip id="apps-mentions" place="top" overlay={i18.t('productMentions.mentionOfProduct')}>
            {(appMentions?.items || [])
              .filter(({ product }) => (product.info?.image || '').length > 0)
              .slice(0, 3)
              .map(({ product }) => (
                <Avatar
                  id={product?.id}
                  key={product?.id}
                  size={36}
                  src={product?.info?.image}
                  defaultAvatar="https://buzzguru.com/assets/images/product.png"
                  shape="square"
                  innerStyle={{
                    borderRadius: 8,
                    border: '2px solid #313137',
                  }}
                  style={{
                    marginRight: -12,
                    background: 'none',
                  }}
                />
              ))}
            {appsTotal - 3 > 0 ? <MoreWrapper>{`+${appsTotal - 3}`}</MoreWrapper> : ''}
          </Tooltip>
        </Link>
      ) : (
        ''
      )}
      {(gameMentions.items || []).length ? (
        <Link target="_blank" href={`https://buzzguru.com/${type}/channels/${item?._id}/games`}>
          <Tooltip id="games-mentions" place="top" overlay={i18.t('gamesMentions.mentionOfGame')}>
            {(gameMentions?.items || []).slice(0, 3).map(({ product }) => (
              <Avatar
                id={product?.id}
                key={product?.id}
                size={36}
                src={product?.info?.image}
                defaultAvatar="https://buzzguru.com/assets/images/product.png"
                shape="square"
                innerStyle={{
                  borderRadius: 8,
                  border: '2px solid #313137',
                }}
                style={{
                  marginRight: -12,
                  background: 'none',
                }}
              />
            ))}
            {gamesTotal - 3 > 0 ? <MoreWrapper>{`+${gamesTotal - 3}`}</MoreWrapper> : ''}
          </Tooltip>
        </Link>
      ) : (
        ''
      )}
      {(mentionsChannels || []).length ? (
          <>
            {(mentionsChannels || []).slice(0, 4).map((channel) => (
              <Tooltip id="channel-mentions" place="top" overlay={channel?.name}>
                <Link key={channel?._id} target="_blank" href={`https://buzzguru.com/${type}/channels/${channel?._id}`}>
                  <Avatar
                    title={channel?.name}
                    src={channel?.avatar}
                    defaultAvatar="https://bzzguru.com/assets/images/blogger.png"
                    size={36}
                    cspStyle={{ marginRight: -12, borderRadius: '50px', border: '2px solid #313137', }}
                    // style={{ marginRight: 12 }}
                  />
                </Link>
              </Tooltip>
            ))}
            {(mentionsChannels || []).length - 4 > 0 ? (
              <Tooltip id="channel-mentions-total" place="top" overlay={i18.t('videoSitesBlock.mentionAll')}>
                <Link target="_blank" href={`https://buzzguru.com/${type}/channels/${item?._id}`}>
                  <MoreWrapper style={{ borderRadius: '50%', marginBottom: 4 }}>{`+${(mentionsChannels || []).length - 4}`}</MoreWrapper>
                </Link>
              </Tooltip>
             ) : ''}
          </>
      ) : (
        ''
      )}
      {(siteMentions.items || []).length ? (
        <Link target="_blank" href={`https://buzzguru.com/${type}/channels/${item?._id}/sites`}>
          <Tooltip id="site-mentions" place="top" overlay={i18.t('productMentions.mentionOfSite')}>
            {sitesTotal > 0 ? (
              <MoreWrapper style={{ width: 'auto', overflow: 'hidden' }}>
                <img alt="Sites" src={SiteLogos} />
                <Typography style={{ padding: '0 8px' }} color="#fff" view="title-2">
                  {i18.t('extention.moreSites', { count: sitesTotal })}  
                </Typography>
              </MoreWrapper>
             ) : (
             ''
             )}
          </Tooltip>
        </Link>
      ) : (
        ''
      )}
    </Wrapper>
  );
};

Mention.propTypes = {
  entity: PropTypes.objectOf(Object).isRequired,
  i18: PropTypes.objectOf(Object).isRequired,
};

export default inject('i18')(Mention);
