// import EntityStore from '@lskjs/mobx/mobxStores/EntityStore';
import { isClient } from '@lskjs/env';
import Err from '@lskjs/utils/Err';
import { createElement } from 'react';

import MainLayout from '../layouts/MainLayout';
import ErrorPage from '../pages/ErrorPage';
import ExtentionPage from '../pages/ExtentionPage';
import LoginPage from '../pages/LoginPage';
// import findChannel from '../stores/ExtentionApi';
import getRequestParams from '../utils/getRequestParams';

export default {
  path: '',
  async action({ next, page, app }) {
    return page.next(next).catch((err) => {
      if (err && !['page.cancel'].includes(err.code)) {
        const pageParams = {
          err,
          app,
        };

        pageParams.children = createElement(ErrorPage, pageParams);

        return page.component(MainLayout, pageParams);
      }
      throw new Err('E_404', err);
    });
  },
  children: [
    {
      path: '',
      children: [
        {
          path: '',
          async action({ page, app }) {
            const ExtentionStore = await app.module('stores.ExtentionStore');
            const reqData = await getRequestParams();
            
            let item;
            if ((reqData?.type || '') === 'youtubeVideo') {
              item = await ExtentionStore.findPost({ id: reqData.id, type: 'youtube' });
            } else if ((reqData?.type || '') === 'twitchStream') {
              item = await ExtentionStore.findPost({ id: reqData.id, type: 'twitch' });
            } else {
              item = await ExtentionStore.findChannel({ ...reqData });
            }
            const login = app.catchError(async (values) => {
              try {
                console.log({values})
                const email = (values?.email || '').toLowerCase();
                const { token } = await ExtentionStore.login({ ...values, email });
                const session = await ExtentionStore.getSession({ token });
                return session;
              } catch (error) {
                const message = error?.response?.data?.message?.error;
                throw new Err({error});
              }
            });
            const pageParams = {
              entity: { item },
              app,
              login
            };

            pageParams.children = createElement(ExtentionPage, pageParams);

            return page.component(MainLayout, pageParams);
          },
        },
      ],
    },
    // {
    //   path: '/login',
    //   children: [
    //     {
    //       path: '',
    //       async action({ page, app }) {
    //         const pageParams = {
    //           app,
    //         };

    //         pageParams.children = createElement(LoginPage, pageParams);

    //         return page.component(MainLayout, pageParams);
    //       },
    //     },
    //   ],
    // },
    {
      path: '(.*)',
      action({ path, page }) {
        const err = {
          status: 404,
          message: `Not found path ${path}`,
          path,
        };
        const pageParams = {
          err,
        };

        pageParams.children = createElement(ErrorPage, pageParams);

        return page.component(MainLayout, pageParams);
      },
    },
  ],
};
