import Tooltip from '@buzzguru/ui/Tooltip';
import Typography from '@buzzguru/ui/Typography2';
import { inject } from 'mobx-react';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';

import getStats from '../../../utils/getStats.jsx';
import getRequestParams from '../../../utils/getRequestParams';
import { Wrapper } from './Stats.styles';

const Stats = ({ entity, i18, ...props }) => {
  const { item } = entity;
  const [type, setType] = useState('youtube');
  const stats = getStats({ item, type, i18 });
  useEffect(() => {
    getRequestParams().then((res) => setType(res.type || 'youtube'));
  }, [document?.location?.href || '']);
  return (
    <Wrapper id="stats" {...props}>
      {stats.map((stat) => (
        <Tooltip key={stat.id} id={stat.id} overlay={stat.title}>
          <div
            style={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              paddingRight: 16,
              flexDirection: 'row',
            }}
          >
            {stat.icon}
            <Typography view="extra-1" style={{ paddingLeft: 8 }} color="#ffffff">
              {stat.value}
            </Typography>
          </div>
        </Tooltip>
      ))}
    </Wrapper>
  );
};

Stats.propTypes = {
  entity: PropTypes.objectOf(Object).isRequired,
  i18: PropTypes.objectOf(Object).isRequired,
};

export default inject('i18')(Stats);
